<template>
  <v-card height="100%">
    <v-card-title>Vials used per team (yesterday)</v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <canvas id="distributionPieChart" width="100%" height="100%"></canvas>
    </v-card-text>
  </v-card>
</template>

<script>
import Chart from 'chart.js/auto'
import axios from 'axios'
import {isYesterday} from 'date-fns'

export default {
  data: () => ({
    chartType: 'pie',
    chartdata: {
      labels: [],
      datasets: [
        {
          backgroundColor: ['#00c853', '#023047'],
          data: []
        }
      ]
    },
  }),

  mounted () {
    this.fetchVialsUsed()
  },

  methods: {
    async fetchVialsUsed() {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }


      try {
        const {data} = await axios.get(`${apiHost}/daily-reports`, config)
        const filtered = data.filter(v => isYesterday(new Date(v.report_date)))
        this.chartdata.labels = filtered.map(v => v.team_name)
        this.chartdata.datasets[0].data = filtered.map(v => v.vials_used)

        new Chart('distributionPieChart', {
          type: this.chartType,
          data: this.chartdata,
        })
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }
      }
    }
  }
}
</script>
