<template>
  <v-card max-width="1200" class="mx-auto">
    <v-card-title>
      Vaccine inventory logs

      <v-spacer />
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="items"
      :loading="isLoadingItems"
      :search="search"
    >
      <template v-slot:item.transaction_type="{item}">
        {{item.transaction_type === 'incoming' ? 'Incoming' : 'Outgoing'}}
      </template>
      <template v-slot:item.created_at="{item}">
        {{formatDate(item.created_at)}}
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import axios from 'axios'
import { format } from 'date-fns'

export default {
  name: 'AdminVaccineLogsTable',
  data: () => ({
    headers: [
      {
        text: 'Transaction',
        value: 'transaction_type',
        filterable: false
      },
      {
        text: 'Amount',
        value: 'amount',
        filterable: false
      },
      { text: 'Manufacturer', value: 'manufacturer', filterable: false },
      { text: 'Serial', value: 'serial' },
      { text: 'Lot number', value: 'lot_no' },
      { text: 'Notes', value: 'comments', filterable: true },
      { text: 'Timestamp', value: 'created_at', filterable: false },
    ],
    alert: {
      isOpen: false,
      type: 'warning',
      message: 'Something went wrong. Please contact your IT.',
      color: 'warning',
    },
    items: [],
    isLoadingItems: false,
    search: '',
  }),

  created () {
    this.fetchUsers()
  },

  methods: {
    async fetchUsers () {
      this.isLoadingItems = true

      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/vaccine-logs`, config)
        this.items = data

        this.isLoadingItems = false
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login')
        }

        this.alert.isOpen = true
      }
    },

    formatDate(date) {
      return format(new Date(date), 'PPpp')
    },
  }
}
</script>
