<template>
  <v-dialog
    persistent
    :value="value"
    @input="value = $event.target.value"
    max-width="700"
  >
    <v-card :loading="isSaving">
      <validation-observer
        ref="observer"
        v-slot="{ invalid }"
      >
        <v-card-title class="text-h5">
          Add Vaccination Schedule
        </v-card-title>

        <v-divider />

        <v-card-text>
          <v-alert
            v-model="alert.isOpen"
            border="left"
            :type="alert.type"
            :color="alert.color"
            dismissible
          >
            {{alert.message}}
          </v-alert>

          <v-row>
            <v-col cols="12">
              <validation-provider
                name="Vaccination Hub Location"
                v-slot="{ errors }"
                rules="required"
              >
                <v-text-field
                  label="Vaccination Hub Location"
                  v-model="forms.location"
                  :disabled="isSaving"
                  :error-messages="errors"
                />
              </validation-provider>
            </v-col>

            <v-col cols="6">
              <v-menu
                v-model="isDateMenuOpen"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <validation-provider
                    name="schedule"
                    v-slot="{ errors }"
                    rules="required"
                  >
                    <v-text-field
                      v-model="forms.schedule"
                      label="Schedule"
                      prepend-inner-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      :error-messages="errors"
                    ></v-text-field>
                </validation-provider>
                </template>

                <v-date-picker
                  v-model="forms.schedule"
                  no-title
                  :min="minimumDate"
                  scrollable
                >
                  <v-spacer></v-spacer>

                  <v-btn
                    text
                    color="primary"
                    @click="isDateMenuOpen = false"
                  >
                    Cancel
                  </v-btn>

                  <v-btn
                    text
                    color="primary"
                    @click="isDateMenuOpen = false"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>

            <v-col cols="6">
              <validation-provider
                name="team"
                v-slot="{ errors }"
                rules="required"
              >
                <v-select
                  label="Team"
                  v-model="forms.team_id"
                  item-text="name"
                  item-value="id"
                  :items="teams"
                  :disabled="isSaving"
                  :error-messages="errors"
                />
              </validation-provider>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red"
            text
            @click="close"
            :disabled="isSaving"
          >
            Cancel
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="save"
            :disabled="invalid || isSaving"
            :loading="isSaving"
          >
            Save
          </v-btn>
        </v-card-actions>
      </validation-observer>
    </v-card>
  </v-dialog>
</template>

<script>
import {required, email} from 'vee-validate/dist/rules';
import {extend, ValidationObserver, ValidationProvider, setInteractionMode} from 'vee-validate'
import {addDays, format} from 'date-fns'
import axios from 'axios'

setInteractionMode('eager')
extend('required', {
  ...required,
  message: '{_field_} cannot be empty',
})
extend('email', {
  ...email,
  message: 'Invalid email format'
})

export default {
  name: 'AddTeamScheduleDialog',
  props: ['value'],

  components: {
    ValidationProvider,
    ValidationObserver,
  },

  data: () => ({
    forms: {
      location: '',
      team_id: undefined,
      schedule: undefined
    },
    alert: {
      isOpen: false,
      type: 'success',
      message: 'Schedule created successfully',
      color: 'success',
    },
    teams: [],
    isOpen: false,
    isSaving: false,
    mainDoh: false,
    isDateMenuOpen: false,
  }),

  created () {
    this.fetchTeams()
  },

  methods: {
    delay(time) {
      return new Promise(resolve => setTimeout(resolve, time))
    },

    async fetchTeams () {
      this.isSaving = true
      const apiHost = process.env.VUE_APP_API_HOST
      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const result = await axios.get(
          `${apiHost}/teams`,
          config
        )

        this.teams = result.data
      } catch (error) {
        this.alert.color = 'warning'

        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        if (error.response.status === 400) {
          this.alert.message = error.response.data.message
        } else {
          this.alert.message = 'Something went wrong. Please contact your IT admin.'
        }
      }

      this.isSaving = false
    },

    async save() {
      this.isSaving = true

      let success = false
      try {
        const apiHost = process.env.VUE_APP_API_HOST
        const token = localStorage.getItem('dms-token')
        const config = {
          headers: { Authorization: `Bearer ${token}` }
        }
        await axios.post(
          `${apiHost}/schedules`,
          this.forms,
          config
        )

        success = true
      } catch (error) {
        this.alert.color = 'warning'

        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        if (error.response.status === 400) {
          this.alert.message = error.response.data.message
        } else {
          this.alert.message = 'Something went wrong. Please contact your IT admin.'
        }

        this.isSaving = false
      }

      this.alert.isOpen = true

      if (success) {
        this.alert.color = 'success'
        await this.delay(3000)
        this.close()
        this.clear()
        this.isSaving = false
        this.$emit('created')
      }
    },

    clear() {
      this.alert = {
        isOpen: false,
        type: 'success',
        message: 'Schedule created successfully',
        color: 'success',
      },
      this.forms.location = ''
      this.forms.team_id = undefined
      this.forms.schedule = undefined
    },

    close() {
      this.$emit('input', false)
    }
  },

  computed: {
    minimumDate () {
      const tomorrow = addDays(new Date(), 1)
      return format(tomorrow, 'yyyy-MM-dd')
    }
  }
}
</script>
