<template>
  <v-dialog
    persistent
    :value="value"
    @input="value = $event.target.value"
    max-width="700"
  >
    <v-card :loading="isSaving">
        <v-card-title class="text-h5">
          Delete {{user.first_name}}'s Account
        </v-card-title>

        <v-divider />

        <v-card-text>
          <v-alert
            v-model="alert.isOpen"
            border="left"
            :type="alert.type"
            :color="alert.color"
            dismissible
          >
            {{alert.message}}
          </v-alert>

          Are you sure you want to delete {{user.first_name}}'s Account?
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red"
            text
            @click="close"
            :disabled="isSaving"
          >
            Cancel
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="save"
            :disabled="isSaving"
            :loading="isSaving"
          >
            Delete
          </v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import axios from 'axios'

export default {
  name: 'UpdateUserDialog',
  props: ['value'],

  data: () => ({
    alert: {
      isOpen: false,
      type: 'success',
      message: 'User deleted successfully',
      color: 'success',
    },
    isSaving: false,
    user: {},
  }),

  methods: {
    delay(time) {
      return new Promise(resolve => setTimeout(resolve, time))
    },

    async save() {
      this.isSaving = true

      let success = false
      try {
        const apiHost = process.env.VUE_APP_API_HOST
        const token = localStorage.getItem('dms-token')
        const config = {
          headers: { Authorization: `Bearer ${token}` }
        };

        await axios.delete(
          `${apiHost}/users/${this.user.id}`,
          config
        )

        success = true
      } catch (error) {
        this.alert.color = 'warning'

        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        if (error.response.status === 400) {
          this.alert.message = error.response.data.message
        } else {
          this.alert.message = 'Something went wrong. Please contact your IT admin.'
        }

        this.isSaving = false
      }

      this.alert.isOpen = true
      if (success) {
        this.alert.color = 'success'
        this.alert.message = 'User deleted successfully'
        await this.delay(3000)
        this.close()
        this.isSaving = false
        this.alert.isOpen = false
        this.$emit('deleted')
      }
    },

    close() {
      this.$emit('input', false)
    },

    setUser (user) {
      this.user = user
    }
  }
}
</script>
