<template>
  <v-container>
    <Breadcrumbs
      title="View Vaccine Inventory (Admin)"
      icon="mdi-needle"
      :items="breadcrumbsItems"
    />
    <DashboardTitle />

    <v-alert
      v-model="alert.isOpen"
      border="left"
      :type="alert.type"
      :color="alert.color"
      dismissible
    >
      {{alert.message}}
    </v-alert>

    <v-row class="mb-6">
      <v-col cols="12">
        <DashboardCountTile
          class="mb-10"
          iconSrc="warehouse.png"
          title="Vaccines in Warehouse"
          subtitle="Quantity of Vaccines in warehouse"
          :value="totalVaccines"
        />
      </v-col>
    </v-row>

    <VaccineLogsTable />
  </v-container>
</template>

<script>
import Breadcrumbs from '../../components/admin/Breadcrumbs.vue'
import DashboardTitle from '@/components/admin/DashboardTitle.vue'
import DashboardCountTile from '../../components/admin/DashboardCountTile.vue'
import axios from 'axios'
import VaccineLogsTable from '../../components/admin/VaccineLogsTable.vue'

export default {
  name: 'AdminManageVaccines',
  components: {
    Breadcrumbs,
    DashboardTitle,
    DashboardCountTile,
    VaccineLogsTable,
  },

  data: () => ({
    breadcrumbsItems: [
      {
        text: 'Home',
        disabled: false,
      },
      {
        text: 'View Vaccine Inventory',
        disabled: false,
      },
    ],
    alert: {
      isOpen: false,
      type: 'warning',
      message: 'Something went wrong. Please contact your IT.',
      color: 'warning',
    },
    totalVaccines: 0,
    vaccineLogs: [],
  }),

  created () {
    this.fetchVaccineLogs()
  },

  methods: {
    async fetchVaccineLogs () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/vaccine-logs`, config)

        this.vaccineLogs = data
        this.totalVaccines = data.reduce(
          (acc, curr) => curr.transaction_type === 'incoming' ? acc + curr.amount : acc - curr.amount,
          0
        )
      } catch (error) {
        console.log('error', error)
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        this.alert.isOpen = true
      }
    },
  }
}
</script>
