<template>
  <v-dialog
    persistent
    :value="value"
    @input="value = $event.target.value"
    max-width="700"
  >
    <v-card :loading="isSaving">
      <validation-observer
        ref="observer"
        v-slot="{ invalid }"
      >
        <v-card-title class="text-h5">
          Update User
        </v-card-title>

        <v-divider />

        <v-card-text>
          <v-alert
            v-model="alert.isOpen"
            border="left"
            :type="alert.type"
            :color="alert.color"
            dismissible
          >
            {{alert.message}}
          </v-alert>

          <v-row>
            <v-col cols="6">
              <validation-provider
                name="First name"
                v-slot="{ errors }"
                rules="required"
              >
                <v-text-field
                  label="First name"
                  v-model="forms.first_name"
                  :disabled="isSaving"
                  :error-messages="errors"
                />
              </validation-provider>
            </v-col>

            <v-col cols="6">
              <validation-provider
                name="Last name"
                v-slot="{ errors }"
                rules="required"
              >
                <v-text-field
                  label="Last name"
                  v-model="forms.last_name"
                  :disabled="isSaving"
                  :error-messages="errors"
                />
              </validation-provider>
            </v-col>

            <v-col cols="6">
              <validation-provider
                name="Email"
                v-slot="{ errors }"
                rules="required|email"
              >
                <v-text-field
                  label="Email"
                  v-model="forms.email"
                  :disabled="isSaving"
                  :error-messages="errors"
                />
              </validation-provider>
            </v-col>

            <v-col cols="6">
              <validation-provider
                name="team"
                v-slot="{ errors }"
                rules="required"
              >
                <v-select
                  label="Team"
                  v-model="forms.team_id"
                  item-text="name"
                  item-value="id"
                  :items="teams"
                  :disabled="isSaving"
                  :error-messages="errors"
                />
              </validation-provider>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red"
            text
            @click="close"
            :disabled="isSaving"
          >
            Cancel
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="save"
            :disabled="invalid || isSaving"
            :loading="isSaving"
          >
            Save
          </v-btn>
        </v-card-actions>
      </validation-observer>
    </v-card>
  </v-dialog>
</template>

<script>
import {required, email} from 'vee-validate/dist/rules';
import {extend, ValidationObserver, ValidationProvider, setInteractionMode} from 'vee-validate'
import axios from 'axios'

setInteractionMode('eager')
extend('required', {
  ...required,
  message: '{_field_} cannot be empty',
})
extend('email', {
  ...email,
  message: 'Invalid email format'
})

export default {
  name: 'UpdateUserDialog',
  props: ['value'],

  components: {
    ValidationProvider,
    ValidationObserver,
  },

  data: () => ({
    userId: -1,
    forms: {
      first_name: '',
      last_name: '',
      email: '',
      team_id: undefined,
    },
    teams: [],
    alert: {
      isOpen: false,
      type: 'success',
      message: 'User updated successfully',
      color: 'success',
    },
    isOpen: false,
    isSaving: false,
  }),

  created () {
    this.fetchTeams()
  },

  methods: {
    delay(time) {
      return new Promise(resolve => setTimeout(resolve, time))
    },

    async fetchTeams () {
      this.isSaving = true
      const apiHost = process.env.VUE_APP_API_HOST
      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const result = await axios.get(
          `${apiHost}/teams`,
          config
        )

        this.teams = result.data
      } catch (error) {
        this.alert.color = 'warning'

        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        if (error.response.status === 400) {
          this.alert.message = error.response.data.message
        } else {
          this.alert.message = 'Something went wrong. Please contact your IT admin.'
        }
      }

      this.isSaving = false
    },

    async save() {
      this.isSaving = true

      let success = false
      try {
        const apiHost = process.env.VUE_APP_API_HOST
        const token = localStorage.getItem('dms-token')
        const config = {
          headers: { Authorization: `Bearer ${token}` }
        }
        await axios.put(
          `${apiHost}/users/${this.userId}`,
          { ...this.forms, type: 'team_lead' },
          config
        )

        success = true
      } catch (error) {
        this.alert.color = 'warning'

        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        if (error.response.status === 400) {
          this.alert.message = error.response.data.message
        } else {
          this.alert.message = 'Something went wrong. Please contact your IT admin.'
        }

        this.isSaving = false
      }

      this.alert.isOpen = true

      if (success) {
        this.alert.color = 'success'
        this.alert.message = 'User updated successfully'
        await this.delay(3000)
        this.close()
        this.reset({})
        this.isSaving = false
        this.$emit('updated')
      }
    },

    reset(user) {
      this.alert = {
        isOpen: false,
        type: 'success',
        message: 'User updated successfully',
        color: 'success',
      },


      this.userId = user.id
      this.forms.first_name = user.first_name
      this.forms.last_name = user.last_name
      this.forms.email = user.email
      this.forms.team_id = user.team_id
    },

    close() {
      this.$emit('input', false)
    }
  }
}
</script>
