<template>
  <v-container>
    <Breadcrumbs
      title="Manage Users (Admin)"
      icon="mdi-account-edit"
      :items="breadcrumbsItems"
    />
    <DashboardTitle />
    <CreateUserDialog v-model="isCreateUserDialogOpen" @created="fetchUsers" />
    <UpdateUserDialog v-model="isUpdateUserDialogOpen" @updated="fetchUsers" ref="updateUserDialogRef" />
    <DeleteUserDialog v-model="isDeleteUserDialogOpen" @deleted="fetchUsers" ref="deleteUserDialogRef" />

    <v-alert
      v-model="alert.isOpen"
      border="left"
      :type="alert.type"
      :color="alert.color"
      dismissible
    >
      {{alert.message}}
    </v-alert>

    <v-card max-width="1200" class="mx-auto">
      <v-card-title>
        List of Users
        <v-spacer />

        <v-btn
          color="primary"
          @click="isCreateUserDialogOpen = true"
        >
          Create new user
        </v-btn>
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="items"
        :loading="isLoadingItems"
      >
        <template v-slot:item.actions="{item}">
          <v-btn color="success" @click="update(item)">
            Update
          </v-btn>

          <v-btn color="error" @click="del(item)">
            Delete
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import Breadcrumbs from '../../components/admin/Breadcrumbs.vue'
import DashboardTitle from '@/components/admin/DashboardTitle.vue'
import CreateUserDialog from '../../components/admin/CreateUserDialog.vue'
import axios from 'axios'
import UpdateUserDialog from '../../components/admin/UpdateUserDialog.vue'
import DeleteUserDialog from '../../components/admin/DeleteUserDialog.vue'

export default {
  name: 'AdminManageUsers',
  components: {
    Breadcrumbs,
    DashboardTitle,
    CreateUserDialog,
    UpdateUserDialog,
    DeleteUserDialog,
  },
  data: () => ({
    isCreateUserDialogOpen: false,
    isUpdateUserDialogOpen: false,
    isDeleteUserDialogOpen: false,
    breadcrumbsItems: [
      {
        text: 'Home',
        disabled: false,
      },
      {
        text: 'Manage Users',
        disabled: false,
      },
    ],
    headers: [
      { text: 'Name', value: 'name' },
      { text: 'Team', value: 'team_name' },
      {
        text: 'Action',
        value: 'actions',
        sortable: false,
        width: '250px'
      },
    ],
    alert: {
      isOpen: false,
      type: 'warning',
      message: 'Something went wrong. Please contact your IT.',
      color: 'warning',
    },
    items: [],
    isLoadingItems: false,
  }),
  created() {
    this.fetchUsers()
  },
  methods: {
    async fetchUsers () {
      this.isLoadingItems = true

      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const result = await axios.get(`${apiHost}/users`, config)

        this.items = result.data.map(this.formatUserData)
        this.isLoadingItems = false
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        this.alert.isOpen = true
      }
    },

    formatUserData (userData) {
      return {
        ...userData,
        name: `${userData.first_name} ${userData.last_name}`,
      }
    },

    update(user) {
      this.isUpdateUserDialogOpen = true
      this.$refs.updateUserDialogRef.reset(user)
    },

    del(user) {
      this.isDeleteUserDialogOpen = true
      this.$refs.deleteUserDialogRef.setUser(user)
    }
  }
}
</script>
